.loginpage {
  width: 100vw;
  height: calc(100vh - 182px);
  // height: calc(100vh - 96px);
  max-width: 1200px;
  margin: 0 auto;
  color: #fff;
  .aboutbox {
    padding-top: 140px;
    img {
      /* logo/color-on-dark */

      width: 256px;
      height: 110px;

      /* Inside auto layout */
    }
    .content {
      margin-top: 60px;
      /* stcubed is a versatile multi-strategy crypto asset manager focusing on crypto and related frontier technologies. */

      width: 768px;
      max-width: 768px;
      height: 48px;

      /* lead/lead italic

subtitles
*/
      font-family: "Chakra Petch";
      font-style: italic;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      /* or 120% */

      color: #f3f4f5;
    }
    .btnbox {
      padding-top: 60px;
      .btn {
        &:hover {
          background-color: #fff;
          span {
            color: #16232a;
          }
        }
        &:active {
          background-color: #07b169;
        }
        /* Button */

        /* button */

        /* Auto layout */

        /* lead/lead emphasis */
        cursor: pointer;
        width: 215px;
        height: 64px;
        background-color: #24dc6e;
        text-align: center;
        transform: skew(-15deg, 0);
        span {
          font-family: "Chakra Petch";
          font-weight: 700;
          font-size: 20px;
          line-height: 64px;
          transform: skew(15deg, 0);

          /* identical to box height, or 120% */

          color: #16232a;
        }
      }
    }
  }
  .box {
    padding-top: 40px;

    .header {
      /* STRATEGY */

      /* header/header 1 */
      font-family: "Michroma";
      font-style: normal;
      font-weight: 400;
      font-size: 32px;
      line-height: 40px;
      /* identical to box height, or 125% */

      /* gradient.title */
      background: linear-gradient(180deg, #7f8790 0%, #4e5c6a 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;

      text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
    }
    .desc {
      /* stcubed implements a diverse approach, engaging in transactions within both the primary and secondary markets while exploring opportunities both onchain and offchain. */

      width: 512px;
      height: 96px;
      font-family: "Chakra Petch";
      font-style: italic;
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      /* or 120% */

      color: #f3f4f5;
    }
  }

  .borderBox {
    padding-top: 80px;
    ul {
      /* Frame 5 */

      /* Auto layout */
      display: flex;
      justify-content: space-between;
      align-items: center;

      padding: 0px;
      gap: 16px;

      width: 1200px;
      height: 320px;

      li {
        /* Frame 22 */
        cursor: pointer;
        /* Auto layout */
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        padding: 32px 32px 32px 24px;

        width: 288px;
        min-width: 220px;
        height: 320px;

        /* gradient.bg */
        background: linear-gradient(180deg, #16232a 0%, #1f2d34 100%);
        /* translucent light border */
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15),
          inset 0px 0px 2px rgba(255, 255, 255, 0.15);
        .title {
          /* 01 */

          width: 232px;
          // height: 206px;

          /* header/header 1 */
          font-family: "Michroma";
          font-style: normal;
          font-weight: 400;
          font-size: 32px;
          line-height: 40px;
          /* or 125% */
          text-align: right;

          color: #24dc6e;
        }
        .bottom {
          /* Deploy majority of our capital in the liquid secondary market */

          width: 232px;
          // height: 40px;

          /* body/body */
          font-family: "Chakra Petch";
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 20px;
          /* or 125% */

          color: #f3f4f5;
        }
      }
    }
  }
  .team {
    padding-top: 40px;
    .text {
      /* team >> */
      cursor: pointer;
      font-family: "Chakra Petch";
      font-style: italic;
      font-weight: 400;
      font-size: 20px;
      line-height: 32px;
      /* identical to box height, or 160% */
      text-align: right;

      color: #24dc6e;
    }
  }
  .teamBox {
    overflow: hidden;
    ul {
      .libox {
        cursor: pointer;
        /* Frame 30 */
        margin: 15px 0;
        /* Frame 30 */

        /* Auto layout */
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: flex-end;
        align-content: center;
        padding: 0px;
        row-gap: 0px;

        width: 100%;
        height: 192px;

        background: #ffb68a;
        /* translucent light border */
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15),
          inset 0px 0px 2px rgba(255, 255, 255, 0.15);
        img {
          /* image */

          /* Auto layout */
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          padding: 0px;
          gap: 10px;

          width: 192px;
          height: 192px;
        }
        .box {
          /* body */

          /* Auto layout */
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          padding: 24px 32px 32px 24px;
          gap: 16px;

          width: calc(100% - 192px);
          min-width: 320px;
          height: 192px;
          min-height: 192px;

          background: #1f2d34;

          /* Inside auto layout */
          flex: none;
          order: 1;
          flex-grow: 1;
          .title {
            /* title */

            width: 648px;
            height: 31px;

            font-family: "Chakra Petch";
            font-style: normal;
            font-weight: 700;
            font-size: 24px;
            line-height: 31px;

            color: #7f8790;

            /* Inside auto layout */
            flex: none;
            order: 0;
            align-self: stretch;
            flex-grow: 0;
          }
          .bottom {
            /* content */

            /* Auto layout */
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            padding: 0px;
            font-family: "Chakra Petch";

            width: 648px;
            height: 80px;

            /* Inside auto layout */
            flex: none;
            order: 1;
            align-self: stretch;
            flex-grow: 0;
            li {
              margin-left: 1rem;
              list-style-type: disc;
            }
          }
        }
      }
    }
  }

  .CONTACTBox {
    li {
      /* Frame 22 */
      margin: 15px 0;
      cursor: pointer;
      /* Auto layout */
      display: flex;
      flex-direction: row;
      align-items: flex-end;
      padding: 32px;
      gap: 8px;

      width: 100%;
      min-width: 220px;
      height: 88px;

      background: #1f2d34;
      /* translucent light border */
      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15),
        inset 0px 0px 2px rgba(255, 255, 255, 0.15);

      /* Inside auto layout */
      flex: none;
      order: 0;
      align-self: stretch;
      flex-grow: 0;
      .title {
        /* content */

        width: 792px;
        height: 24px;

        /* lead/lead italic

subtitles
*/
        font-family: "Chakra Petch";
        font-style: italic;
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        /* identical to box height, or 120% */

        color: #f3f4f5;

        /* Inside auto layout */
        flex: none;
        order: 0;
        flex-grow: 1;
      }
      .bottom {
        /* /// */

        width: 32px;
        height: 24px;

        /* lead/lead emphasis */
        font-family: "Chakra Petch";
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        /* identical to box height, or 120% */
        text-align: right;

        color: #24dc6e;

        /* Inside auto layout */
        flex: none;
        order: 1;
        flex-grow: 0;
      }
    }
  }
  //   background: url(../../static/bg.png) no-repeat center center;
}

@primary-color: #467DFD;